.topPerspective{
  background-color: rgb(117, 198, 252);
  width:100%;
  height:20px;
  border: solid 0px red;
  -webkit-clip-path: polygon(50% 0%, 93% 0, 82% 100%, 18% 100%, 8% 0);
          clip-path: polygon(50% 0%, 93% 0, 82% 100%, 18% 100%, 8% 0);
}

@-webkit-keyframes AddingAnimation{
  0%{
      -webkit-transform: rotate(0) translateY(-20%);
              transform: rotate(0) translateY(-20%);
      opacity: 0;
      }
  100%{
      -webkit-transform: rotate(0) translateY(0);
              transform: rotate(0) translateY(0);
      opacity: 1;
      }
}

@keyframes AddingAnimation{
  0%{
      -webkit-transform: rotate(0) translateY(-20%);
              transform: rotate(0) translateY(-20%);
      opacity: 0;
      }
  100%{
      -webkit-transform: rotate(0) translateY(0);
              transform: rotate(0) translateY(0);
      opacity: 1;
      }
}

.App-intro {
  margin:auto;
  text-align:center;
  display:grid;
  grid-gap: 30px;
  grid-auto-columns: 90%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  border: solid 0px red;
}

@-webkit-keyframes shine {
  to {
    background-position: 200% center;
  }
}

@keyframes shine {
  to {
    background-position: 200% center;
  }
}

.App-title {
  border: solid 0px red;
  font-size: 5vmax;
	font-family: 'Poppins', sans-serif;
  background: -webkit-gradient(linear, right top, left top, color-stop(20%, lightgreen), color-stop(40%, red), color-stop(60%, red), color-stop(80%, lightgreen));
  background: -webkit-linear-gradient(right, lightgreen 20%, red 40%, red 60%, lightgreen 80%);
  background: -o-linear-gradient(right, lightgreen 20%, red 40%, red 60%, lightgreen 80%);
  background: linear-gradient(to left, lightgreen 20%, red 40%, red 60%, lightgreen 80%);
  background-size: 200% auto;  
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: shine 20s linear infinite;
          animation: shine 20s linear infinite;
}

/* The container <div> - needed to position the dropdown content */
.search-dropdown {
  position: relative;
  margin:auto;
}

input[id=new-location] {
  position:relative;
  -webkit-box-shadow: 2px 8px 16px 2px rgba(0,0,0,0.2);
          box-shadow: 2px 8px 16px 2px rgba(0,0,0,0.2);
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  border: 1px solid transparent;
  background-color: #f1f1f1;
  padding: 5px 5px 5px 5px;
  font-size: 14px;
  background-color: #f1f1f1;
  width: 100%;
  border-radius: 5px;
  font-size:8pt;
}


@media only screen and (min-width:600px) {
  .search-dropdown {
    width:500px;
  }  
}


